body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background: linear-gradient(to right,#2c5364, #203a43, #0f2027);
  font-family: "Be Vietnam Pro";
  color: white;
}

.title {
  color: white;
  text-align: center;
  font-size: 25px;
  font-family: "Be Vietnam Pro";
  padding-top: 15px;
}

.coin-image {
 height: 150px;
 width: auto;
 padding-right: 25px;
}

.coindata-container {
  padding-left: 25px;
}

.link {
  text-decoration: none;
}

.home-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-bottom: 25px;
  padding-top: 25px;
}

.coin-name {
  padding-left: 25px;
  padding-right: 25px;
}

.form-container {
  text-align: center;
}

.search-btn {
  font-family: 'Roboto', sans-serif;
  border-radius: 4px;
  border: none;
  margin-top: 25px;
  margin-bottom: 25px;
  width: 275px;
  height: 40px;
  padding-left: 15px;
}
body {
    background: linear-gradient(to right,#2c5364, #203a43, #0f2027);
}

.title {
    text-align: center;
    font-family: "Be Vietnam Pro";
    font-size: 24px;
    padding-top: 15px;
    padding-bottom: 15px;
}

.main-container {
    background-color: white;
    width: 70%;
    margin: auto;
    border-radius: 15px;
}

.row-container {
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: auto;
}

.text {
    font-family: "Be Vietnam Pro";
    font-weight: 400;
    font-size: 1.25rem;
}

.data {
    font-family: "Be Vietnam Pro";
    font-weight: 600;
    text-align: center;
    font-size: 1rem;
}

.price-increase {
    color: green;
    font-size: 1rem;
}
  
.price-decrease {
    color: red;
    font-size: 1rem;
}

.link {
    text-decoration: none;
}

.supply { 
    font-size: 1rem;;
}

